import StopQuantity, { Values } from 'constants/StopQuantity'

export default function (value) {
  switch (value) {
    case StopQuantity[Values.ALL]: {
      return ''
    }
    case StopQuantity[Values.DIRECT]: {
      return ' '
    }
    case StopQuantity[Values.ONE]: {
      return '1 '
    }
    default: {
      return `${value} `
    }
  }
}
